body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* border: 1px solid #555; */
}

body {
  width: 100vw;
  background: #222;
}

.App {
  width: 100%;
  min-height: 100vh;
  position: relative;
  text-align: center;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

.noisyBackground {
  position: absolute;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.noisyBackground::after {
  content: "";
  position: fixed;
  width: 200vw;
  height: 200vh;
  top: -50%;
  left: -50%;
  background-image: url(/kznCORP/jerichofillon/static/media/noise.d13ed368.png);
  opacity: 0.4;
  z-index: -1;
}

/* Tunnel */
#tunnelWrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: -1;
}

/* Media Queries */

/* Devices with 768px width and below. (Phones and Tablets) */
@media only screen and (max-width: 768px) {
  .tunnelSvg {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

@media only screen and (max-width: 468px) {
  #tunnelWrapper {
    top: 46%;
  }

  .tunnelSvg {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}

/* Portfolio Container */
#portfolio {
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  overflow: hidden;
}

#portfolioContainer {
  width: 100%;
  margin-left: 2em;
  margin-right: 2em;
}

.sloganDesc {
  text-align: start;
  color: #ddd;
  font-size: 1.1em;
  letter-spacing: -0.3px;
  font-weight: 700;
}

/* Portfolio Elements */
.portfolioMenu {
  width: 100%;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

.portfolioLinkWrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 0.25em;
}

.portfolioLink {
  position: relative;
  width: 30%;

  display: flex;
  align-items: baseline;
  justify-content: space-between;

  color: #555;
  font-size: 0.9em;
  text-transform: uppercase;
}

#playground {
  margin-top: 1.75em;
  margin-bottom: 3em;
}

/* Portfolio Description */
.portfolioDesc {
  display: none;
  width: 30%;
}

.portfolioLink:hover,
.portfolioLink:focus,
.portfolioLink:active {
  margin-top: 0.75em;
  background: -webkit-linear-gradient(left, #2c73fe, #89cff0);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.portfolioLink:hover + .portfolioDesc,
.portfolioLink:focus + .portfolioDesc,
.portfolioLink:active + .portfolioDesc {
  display: block;
}

#desc {
  color: #bbb;
  font-weight: 400;
  text-align: start;
  font-size: 0.9em;
}

/* Used for displaying a bullet point beside PF elements */
/* .portfolioLink:hover > .activeLink {
  display: block;
  position: absolute;
  color: #fff;
  left: -1.25em;
  font-weight: 900;
} */

/* Devices with 768px width and below. (Phones and Tablets) */
@media only screen and (max-width: 768px) {
  #portfolioContainer {
    margin-left: 1.2em;
    margin-right: 1.2em;
  }

  #slogan {
    margin-bottom: 1.25em;
  }

  .sloganDesc{
    font-size: 0.95em;
  }

  .portfolioLinkWrapper {
    display: block;
  }

  .portfolioLink {
    width: 100%;
    font-size: 0.8em;
  }

  .portfolioDesc {
    width: 100%;
    margin-top: 0.5em;
    margin-bottom: 1em;
  }

  #playground {
    margin-top: 1.5em;
    margin-bottom: 2em;
  }

  #desc {
    font-size: 0.75em;
  }
}

/* Footer */
.copyrightWrapper {
  position: absolute;
  bottom: 2%;
  right: 1.5em;
}

#copyright {
  color: #555;
  font-size: 0.8em;
}

#streak {
  position: absolute;
  bottom: 0%;
  background-color: #2c73fe;
  height: 5px;
  width: 100vw;
}

/* Devices with 768px width and below. (Phones and Tablets) */
@media only screen and (max-width: 768px) {
  .copyrightWrapper {
    display: none;
  }
}

/* Header */
#header {
  position: fixed;
  width: 100%;
  color: #fff;
  margin-top: 1.75em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.headerNameWrapper {
  height: 100%;
  margin-left: 2em;
  display: flex;
  align-items: flex-end;
}

#headerName {
  font-size: 1.5em;
  text-transform: uppercase;
  letter-spacing: -0.02em;
}

#headerPronoun {
  color: #555;
  margin-left: 1em;
  margin-bottom: 0.25em;
  font-size: 0.8em;
  font-weight: 200;
  letter-spacing: -0.02em;
}

#headerInfoLink {
  height: 100%;
  margin-right: 2em;
  color: #fff;
  text-decoration: none;
}

.headerInfo {
  display: flex;
  align-items: center;
  text-align: center;
  font-size: 0.8em;
}

#headerInfo {
  text-transform: uppercase;
  letter-spacing: -0.02em;
  margin-right: 0.5em;
}

.hidden {
  color: #222 !important;
  background: #222 !important;
}

/* Devices with 768px width and below. (Phones and Tablets) */
@media only screen and (max-width: 768px) {
  #header {
    margin-top: 1.25em;
  }

  .headerNameWrapper {
    margin-left: 1.25em;
  }

  #headerName {
    font-size: 1em;
    letter-spacing: -0.03em;
  }

  #headerPronoun {
    margin-left: 1em;
    font-size: 0.6em;
    font-weight: 200;
  }

  #headerInfoLink {
    margin-right: 1.25em;
  }

  .headerInfo {
    font-size: 0.7em;
  }
}

/* Info Page */
#infoContent {
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  text-align: start;
  margin-left: 2em;
  margin-right: 2em;
}

#info {
  width: 50%;

  display: flex;
  flex-direction: column;
  grid-row-gap: 1.25em;
  row-gap: 1.25em;
  align-items: flex-start;
  justify-content: center;
}

/* Avatar */
img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.avatar {
  width: 5rem;
}

/* Name */
.infoNameWrapper {
  display: flex;
  align-items: flex-end;
}

#infoName {
  color: #fff;
  font-size: 1.75em;
  text-transform: uppercase;
  letter-spacing: -0.02em;
}

#infoPronoun {
  color: #555;
  margin-left: 1em;
  margin-bottom: 0.25em;
  font-size: 0.8em;
  font-weight: 200;
  letter-spacing: -0.02em;
}

/* Description */
.infoOccupation {
  width: 60%;
}

.infoSlogan {
  width: 80%;
}

.infoDesc {
  position: relative;
  color: #fff;
  letter-spacing: -0.2px;
  text-align: start;
}

.iconWrapper {
  display: flex;
  align-items: center;
  margin-top: 0.3em;
  grid-gap: 1em;
  gap: 1em;
}

#conceptDesign {
  display: flex;
  align-items: center;
  padding-left: 0.15em;
  width: 2.2em;
  height: 1.2em;
  background-color: #777;
  border-radius: 5em;
}

#finalDeliverables {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 0.15em;
  width: 2.2em;
  height: 1.2em;
  background-color: #66da64;
  border-radius: 5em;
}

.switch {
  display: inline-block;
  width: 1em;
  height: 1em;
  background-color: #fff;
  border-radius: 5em;
}

.infoMore {
  margin-top: 0.5em;
  width: 75%;
}

#moreDesc {
  color: #999;
  font-weight: 600;
}

#gradientText {
  background: -webkit-linear-gradient(left, #2c73fe, #b9ffb8);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#tenure {
  background: -webkit-linear-gradient(90deg, #2c73fe, #66da64);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Devices with 1200px width and below. (Laptops and Desktops) */
@media only screen and (max-width: 1400px) {
  .infoOccupation {
    width: 80%;
  }

  .infoSlogan {
    width: 100%;
  }
}

/* Devices with 768px width and below. (Phone and Tablets) */
@media only screen and (max-width: 768px) {
  #infoContent {
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 4em;
    margin-left: 1.25em;
    margin-right: 1.25em;
  }

  #info {
    width: 100%;
    grid-row-gap: 2em;
    row-gap: 2em;
    margin-top: 4em;
  }

  .avatar {
    position: relative;
    left: -1em;
    bottom: -1.5em;
    -webkit-transform: scale(0.65);
            transform: scale(0.65);
  }

  .infoNameWrapper {
    display: none;
  }

  .infoOccupation,
  .infoSlogan,
  .infoMore {
    width: 100%;
  }
}

/* Contact Page */
#contactMe {
  width: 50%;

  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.contactInfo {
  color: #fff;
  font-size: 1.25em;
  text-align: center;
  letter-spacing: -0.2px;
}

.contactMailWrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.5em;
}

.contactMail {
  position: absolute;
  color: #fff;
  font-size: 0.95em;
  background-color: transparent;
  border-style: none;
}

.contactMail:hover {
  cursor: pointer;
}

.contactScheduleWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.5em;
}

.contactSchedule {
  background-color: #222;
  border: none;
}

#consultation h4 {
  color: #aaa;
  font-weight: 200;
  font-size: 0.8em;
  text-decoration: underline;
}

#consultation:hover > h4 {
  color: #fff;
  text-decoration: underline;
  cursor: pointer;
}

/* Devices with 1200px width and below. (Laptops and Desktops) */
@media only screen and (max-width: 1400px) {
  .contactWrapper {
    width: 60%;
  }

  .contactSchedule {
    font-size: 0.8em;
    letter-spacing: -0.2px;
  }
}

/* Devices with 768px width and below. (Landscape Tablets) */
@media only screen and (max-width: 768px) {
  #contactMe,
  .contactWrapper {
    width: 100%;
    text-align: center;
    font-size: 0.95em;
    margin-top: 5em;
  }

  .contactMail {
    font-size: 1em;
  }
}

